/**
 * PortfolioPage.tsx
 * @file PortfolioPage.tsx
 * @description PortfolioPage.tsx is a React component for the portfolio page of the portfolio website.
 * It is a functional component that returns a div with the class name "portfolio".
 */

/**
 * Importing necessary modules to make the component work
 */
import React, { useEffect, useRef } from "react";
import "../css/PortfolioPage.css";

/**
 * Importing images
 */
import florianC from "../images/florianC.jpg";
import FlcheBas from "../images/FlcheBas.png";
import FlcheHaut from "../images/FlcheHaut.png";
import ExperiencesDiv from "./ExperiencesDiv";
import biosphere7 from "../images/biosphere7.png";
import odomo from "../images/odomo.png";
import machine from "../images/machine.png";
import sql from "../images/SQL.png";
import web from "../images/web.png";
import swot from "../images/swot.png";
import symfony from "../images/symfony.png";
import kifli from "../images/kifli.png";
import associativeArray from "../images/associativeArray.png";
import { Divider, Steps } from "antd";

/**
 * PortfolioPage component that returns the portfolio page of the portfolio website
 * @returns PortfolioPage component
 */
const PortfolioPage: React.FC = () => {
  /**
   * Reference to the experiences section of the page
   */
  const experiencesRef = useRef(null);

  /**
   * Reference to the Odomo section
   */
  const odomoRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Biosphere 7 section
   */
  const biosphere7Ref = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Machine Virtuelle section
   */
  const machineRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the SQL section
   */
  const sqlRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Site Web section
   */
  const webRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the SWOT section
   */
  const swotRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Symfony section
   */
  const symfonyRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Chiffrement de Kifli section
   */
  const kifliRef = useRef<HTMLDivElement>(null);

  /**
   * Reference to the Tableau associatif section
   */
  const associativeArrayRef = useRef<HTMLDivElement>(null);

  /**
   * State to keep track of the number of steps in the portfolio
   */
  const [currentSteps, setCurrentSteps] = React.useState(0);

  useEffect(() => {
    /**
     * Getting the arrow, nav and steps elements
     */
    const arrow = document.querySelector(".arrow-scroll-top");
    const nav = document.querySelector(".nav-bar");
    const steps = document.querySelector(".portfolio-experiences-steps");

    /**
     * Intersection Observer to change the style of the page when scrolling
     */
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            if (arrow) {
              arrow.classList.add("visible");
            }
            if (nav) {
              nav.classList.add("background");
            }
            if (steps) {
              steps.classList.add("visible");
            }
          } else {
            if (arrow) {
              arrow.classList.remove("visible");
            }
            if (nav) {
              nav.classList.remove("background");
            }
            if (steps) {
              steps.classList.remove("visible");
            }
          }
        });
      },
      { threshold: 0.05 }
    );

    /**
     * Observing the experiences section
     */
    const observerExperience = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            if (entry.target === odomoRef.current && odomoRef.current) {
              odomoRef.current.classList.add("visible");
              setCurrentSteps(0);
            }
            if (
              entry.target === biosphere7Ref.current &&
              biosphere7Ref.current
            ) {
              biosphere7Ref.current.classList.add("visible");
              setCurrentSteps(1);
            }
            if (entry.target === machineRef.current && machineRef.current) {
              machineRef.current.classList.add("visible");
              setCurrentSteps(2);
            }
            if (entry.target === sqlRef.current && sqlRef.current) {
              sqlRef.current.classList.add("visible");
              setCurrentSteps(3);
            }
            if (entry.target === webRef.current && webRef.current) {
              webRef.current.classList.add("visible");
              setCurrentSteps(4);
            }
            if (entry.target === swotRef.current && swotRef.current) {
              swotRef.current.classList.add("visible");
              setCurrentSteps(5);
            }
            if (entry.target === symfonyRef.current && symfonyRef.current) {
              symfonyRef.current.classList.add("visible");
              setCurrentSteps(6);
            }
            if (entry.target === kifliRef.current && kifliRef.current) {
              kifliRef.current.classList.add("visible");
              setCurrentSteps(7);
            }
            if (
              entry.target === associativeArrayRef.current &&
              associativeArrayRef.current
            ) {
              associativeArrayRef.current.classList.add("visible");
              setCurrentSteps(8);
            }
          }
        });
      },
      { threshold: 0.4 }
    );

    const experience = experiencesRef.current;
    if (experience) {
      observer.observe(experience);
    }

    const odomo = odomoRef.current;
    if (odomo) {
      observerExperience.observe(odomo);
    }

    const biosphere7 = biosphere7Ref.current;
    if (biosphere7) {
      observerExperience.observe(biosphere7);
    }

    const machine = machineRef.current;
    if (machine) {
      observerExperience.observe(machine);
    }

    const sql = sqlRef.current;
    if (sql) {
      observerExperience.observe(sql);
    }

    const web = webRef.current;
    if (web) {
      observerExperience.observe(web);
    }

    const swot = swotRef.current;
    if (swot) {
      observerExperience.observe(swot);
    }

    const symfony = symfonyRef.current;
    if (symfony) {
      observerExperience.observe(symfony);
    }

    const kifli = kifliRef.current;
    if (kifli) {
      observerExperience.observe(kifli);
    }

    const associativeArray = associativeArrayRef.current;
    if (associativeArray) {
      observerExperience.observe(associativeArray);
    }

    return () => {
      if (experience) {
        observer.unobserve(experience);
      }
      if (odomo) {
        observerExperience.unobserve(odomo);
      }
      if (biosphere7) {
        observerExperience.unobserve(biosphere7);
      }
      if (machine) {
        observerExperience.unobserve(machine);
      }
      if (sql) {
        observerExperience.unobserve(sql);
      }
      if (web) {
        observerExperience.unobserve(web);
      }
      if (swot) {
        observerExperience.unobserve(swot);
      }
      if (symfony) {
        observerExperience.unobserve(symfony);
      }
      if (kifli) {
        observerExperience.unobserve(kifli);
      }
      if (associativeArray) {
        observerExperience.unobserve(associativeArray);
      }
    };
  }, []);

  /**
   * Function to scroll to the experiences section
   */
  const handleScrollExperiences = () => {
    const experiences = document.querySelector(".portfolio-experiences");
    if (experiences) {
      const top = experiences.getBoundingClientRect().top - 120;
      window.scrollBy({ top, behavior: "smooth" });
    }
  };

  /**
   * Function to scroll to a specific experience section
   */
  const handleScroll = (section: HTMLDivElement | null, current: number) => {
    if (section) {
      const top = section.getBoundingClientRect().top - 120;
      window.scrollBy({ top, behavior: "smooth" });
    }
    setCurrentSteps(current);
  };

  /**
   * Function to scroll to the top of the page
   */
  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
    <div className="portfolio">
      <div className="portfolio-welcome">
        <div className="portfolio-welcome-photo">
          <img src={florianC} alt="florianC" />
        </div>
        <div className="portfolio-welcome-text">
          <h1>Bonjour ! Je m'appelle Florian Chapoulli-Pino.</h1>
          <h2>Bienvenue sur mon portfolio !</h2>
          <h2>Bonne visite.</h2>
          <div className="arrow-experiences">
            <img
              src={FlcheBas}
              onClick={handleScrollExperiences}
              alt="scroll-experiences-arrow"
            />
          </div>
        </div>
      </div>
      <div className="arrow-scroll-top">
        <img
          src={FlcheHaut}
          onClick={handleScrollTop}
          alt="scroll-top-arrow"
        />
      </div>
      <div className="portfolio-experiences-steps">
        <Steps
          className="portfolio-steps"
          direction="horizontal"
          size="small"
          current={currentSteps}
          onChange={(current) =>
            handleScroll(
              current === 0
                ? odomoRef.current
                : current === 1
                ? biosphere7Ref.current
                : current === 2
                ? machineRef.current
                : current === 3
                ? sqlRef.current
                : current === 4
                ? webRef.current
                : current === 5
                ? swotRef.current
                : current === 6
                ? symfonyRef.current
                : current === 7
                ? kifliRef.current
                : associativeArrayRef.current,
              current
            )
          }
          responsive={false}
          items={[
            {
              title: "Odomo",
            },
            {
              title: "Biosphere 7",
            },
            {
              title: "Machine Virtuelle",
            },
            {
              title: "SQL",
            },
            {
              title: "Site Web",
            },
            {
              title: "SWOT",
            },
            {
              title: "Symfony",
            },
            {
              title: "Chiffrement de Kifli",
            },
            {
              title: "Tableau associatif",
            },
          ]}
        />
      </div>
      <div className="portfolio-experiences" ref={experiencesRef}>
        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="left"
        >
          Odomo
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              La ralisation de ce projet a t pour moi une vritable
              opportunit de dmontrer les comptences que je vais dvelopper
              tout au long de ma carrire professionnelle. J'ai pu identifier
              les moyens ncessaires pour rpondre aux besoins d'un client 
              l'aide d'un langage de programmation, tout en fournissant un
              rapport dtaill et en expliquant clairement le code et son
              fonctionnement.
              <br />
              <br /> Ce projet m'a apport de nombreux bnfices, tels que la
              mise en place d'un planning optimis pour un travail d'quipe
              efficace dans des conditions optimales, ainsi que le dveloppement
              d'un sens des responsabilits, tant donn l'importance de livrer
              un code fonctionnel qui rpond aux attentes du client.{" "}
            </p>
          }
          image={odomo}
          position="left"
          className="even"
          ref={odomoRef}
          experienceLink="experiences/retourOdomo.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="center"
        >
          Biosphere 7
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet a t extrmement enrichissant pour moi en termes
              d'apprentissage. Au cours des deux phases du projet, j'ai
              notamment contribu  la mise en place de deux stratgies
              destines  une intelligence artificielle.
              <br />
              <br />
              Cela m'a permis de comprendre comment aborder une tche donne,
              qu'il s'agisse du cahier des charges et des rgles associes ou
              des mthodes de travail  adopter. <br />
              <br />
              Le principal bnfice de ce projet a t, pour moi, l'veil de mon
              esprit logique, qui m'a permis de dterminer quelles actions
              taient possibles et prfrables selon chaque situation.
            </p>
          }
          image={biosphere7}
          position="right"
          className="odd"
          ref={biosphere7Ref}
          experienceLink="experiences/retourBiosphere.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="right"
        >
          Machine Virtuelle
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet consistait  installer une machine virtuelle pour
              permettre  une quipe de dveloppeurs de crer un nouveau jeu en
              utilisant le langage Go, qu'il a donc fallu installer, ainsi qu'un
              IDE pour l'utiliser.
              <br />
              <br />
              L'objectif tait d'installer une machine avec un matriel adapt,
              ainsi que de configurer un compte administrateur et un compte
              dveloppeur, chacun ayant des droits spcifiques selon leur
              statut. Il a galement t ncessaire de mettre en place l'outil
              Git pour effectuer un git clone et de modifier le prompt Bash pour
              afficher l'tat de ce Git depuis n'importe o.
              <br />
              <br />
              Ce projet nous a permis,  mes camarades et moi, de matriser
              l'installation d'une machine selon des caractristiques bien
              dfinies, ainsi que de comprendre le fonctionnement d'un terminal
              de commande et comment le modifier pour y afficher des
              informations utiles.
            </p>
          }
          image={machine}
          position="left"
          className="even"
          ref={machineRef}
          experienceLink="experiences/retourMachine.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="left"
        >
          SQL
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce travail de groupe dans le cadre d'un projet universitaire m'a
              permis de dvelopper mes connaissances et de matriser
              adquatement le langage SQL.
              <br />
              <br />
              Le dfi principal de ce projet tait d'tablir une cohsion
              d'quipe avec mon binme, en veillant  se rpartir les tches de
              manire efficace pour optimiser le temps de travail. Au-del de
              cet esprit d'quipe, l'aspect le plus crucial de cette activit a
              t de garantir la qualit des donnes, en s'assurant qu'elles
              soient cohrentes, compltes et prcises.
              <br />
              <br />
              Ainsi, mes comptences en bases de donnes se sont
              considrablement amliores, notamment en ce qui concerne la
              cration et le remplissage d'une base de donnes, ainsi que
              l'laboration d'un schma entit-association.
            </p>
          }
          image={sql}
          position="right"
          className="odd"
          ref={sqlRef}
          experienceLink="experiences/retourSQL.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="center"
        >
          Site Web
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet m'a permis de dvelopper mon esprit cratif et de
              matriser de manire approfondie les langages HTML et CSS pour
              raliser un site web pour une entreprise.
              <br />
              <br />
              Le site devait respecter des critres prcis et la lgislation en
              vigueur, car il s'agissait d'une socit produisant des boissons
              alcoolises. J'ai ainsi appris  intgrer les normes lgales
              requises pour un site internet, tout en veillant  crer une
              interface ergonomique et pratique pour le client.
              <br />
              <br />
              Cette exprience a t pour moi une grande leon, m'enseignant non
              seulement le HTML et le CSS, mais aussi l'ergonomie web et les
              rglementations  respecter dans ce domaine.
            </p>
          }
          image={web}
          position="left"
          className="even"
          ref={webRef}
          experienceLink="experiences/retourWeb.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="right"
        >
          SWOT
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet m'a permis de mieux apprhender ma vision d'une
              entreprise.
              <br />
              <br />
              En collaboration avec mes camarades, nous avons ralis une
              analyse fonctionnelle d'une entreprise. De plus, nous avons
              identifi les diffrentes opportunits et menaces auxquelles elle
              tait confronte.
              <br />
              <br />
              J'en retire de nombreux aspects positifs, tels que la mise en
              place d'un planning optimis pour travailler en quipe dans de
              bonnes conditions, ainsi que la matrise des outils de recherche.
              Il tait crucial de fournir des informations vrifies et
              pertinentes afin de mieux comprendre l'environnement de
              l'entreprise.
            </p>
          }
          image={swot}
          position="right"
          className="odd"
          ref={swotRef}
          experienceLink="experiences/retourSWOT.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="left"
        >
          Symfony
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet m'a permis de dvelopper mon esprit cratif et de
              matriser de manire approfondie les langages HTML et CSS, ainsi
              que PHP avec le framework Symfony, pour raliser une application
              full-stack.
              <br />
              <br />
              L'application devait respecter des critres prcis dfinis lors
              d'une runion avec le Product Owner. J'ai ainsi appris  crer un
              site ergonomique et pratique pour le client.
              <br />
              <br />
              Cette exprience a t pour moi une grande leon, m'enseignant non
              seulement le PHP et l'utilisation de Symfony, mais aussi
              l'ergonomie web et les normes  respecter dans ce domaine.
            </p>
          }
          image={symfony}
          position="left"
          className="even"
          ref={symfonyRef}
          experienceLink="experiences/retourSymfony.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="center"
        >
          Chiffrement de Kifli
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet m'a permis de dvelopper mes comptences en Python et de
              comprendre le fonctionnement du chiffrement de Kifli.
              <br />
              <br />
              Le dfi principal de ce projet tait de comprendre le
              fonctionnement de l'algorithme de chiffrement de Kifli et de
              l'implmenter en Python.
              <br />
              <br />
              Ainsi, mes comptences en Python se sont considrablement
              amliores, notamment en ce qui concerne la manipulation des
              chanes de caractres et la gestion des fichiers.
            </p>
          }
          image={kifli}
          position="right"
          className="odd"
          ref={kifliRef}
          experienceLink="experiences/retourKifli.pdf"
        />

        <Divider
          className="portfolio-experiences-divider"
          type="horizontal"
          orientation="right"
        >
          Tableau associatif
        </Divider>

        <ExperiencesDiv
          text={
            <p>
              Ce projet consistait  crer notre propre tableau associatif en
              Java, en ralisant deux implmentations : une statique et une
              dynamique. L'objectif tait ensuite de comparer les performances
              des deux implmentations  l'aide de benchmarks.
              <br />
              <br />
              La premire implmentation statique utilisait un tableau de taille
              fixe pour stocker les cls et les valeurs associes. La deuxime
              implmentation dynamique utilisait une structure de donnes telle
              qu'une liste chane ou un arbre pour permettre une taille
              variable du tableau associatif.
              <br />
              <br />
              Ce projet nous a permis de mieux comprendre les concepts de base
              des tableaux associatifs et d'apprcier les avantages et les
              inconvnients des diffrentes approches d'implmentation.
            </p>
          }
          image={associativeArray}
          position="left"
          className="even"
          ref={associativeArrayRef}
          experienceLink="experiences/retourTableauAssociatif.pdf"
        />
      </div>
      <footer>
        <p> 2024 Florian Chapoulli-Pino</p>
      </footer>
    </div>
  );
};

export default PortfolioPage;
